<template>
    <div class="list" v-loading="loading">
        <div class="list_head" v-if="user.role != 2">
            <div class="left-button">
                <el-button type="primary" size="small" icon="el-icon-arrow-left" plain @click="goBack"
                    style="color: #000000;border-width: 0px;background-color: unset;font-weight: 500;">{{ lang.back
                    }}</el-button>
            </div>
            <div class="design-style-preference">
                <span class="text">{{ lang.tips10 }}</span>
            </div>
            <div class="preference">
                <div class="upload">
                    <router-link :to="getDesignPreferenceLink()">
                        <el-button style="background: #345CA0; color:white;" class="el-button">{{lang.up}}</el-button>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="list_item">
            <div class="nodata" v-if="list.length == 0"><el-empty :description="lang.nodata"></el-empty></div>
            <div class="box" v-for="item in list" :key="item.id">
                <div class="main" @click="goProject(item.id)">
                    <div class="cover" v-loading="item.loading" :element-loading-text="lang.loading + '...'">
                        <img class="project_cover"
                            :src="'https://www.simplerevise.com/img/project/testimg/thumbnail/' + item.img_url + '.jpg'"
                            alt="">
                    </div>
                </div>
                <div class="title">
                    <div class="action">
                        <i class="el-icon-delete" @click="del(item.img_url)"></i>
                    </div>
                    <p>{{ item.group_name }}</p>
                    <p>{{ item.user_name }} · {{ item.create_time }}</p>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import timer from '../tools/timer';
import global from '@/tools/global';
export default {
    data() {
        return {
            user: {},
            list: [],
            selectedOrganisation: '',
            selectedDesignType: '',
            selectedPreference: '',
            organisations: [
                { id: 1, name: 'Org 1' },
                { id: 2, name: 'Org 2' },
                { id: 3, name: 'Org 3' }
            ],
            designTypes: [
                { id: 1, name: 'Design 1' },
                { id: 2, name: 'Design 2' },
                { id: 3, name: 'Design 3' }
            ],
            preferences: [
                { id: 1, name: 'Preference 1' },
                { id: 2, name: 'Preference 2' },
                { id: 3, name: 'Preference 3' }
            ]
        };
    },
    created() {
        this.lang = this.$lang('board');
        this.user = JSON.parse(localStorage.getItem('designer_user'));

        // 没有参与项目
        if (this.user.projects.substring(1).length == 0) {
            this.list = [];
            this.loading = false;
            return;
        }

        this.getList();

    },
    methods: {
        getList() {
            // 根据id列表获取项目信息
            this.user.projects = this.user.projects.substring(1);
            if (this.user.projects[0] == ',') {
                this.user.projects = this.user.projects.substring(1);
            }
            this.$ajax.get('/project/list_library/?id=' + this.$route.query.id).then(r => {
                this.list = r.map(item => {
                    item.create_time = timer.time('y-m-d', item.create_time * 1000);
                    item.loading = true;
                    return item;
                });

                // 处理封面展示方式，横图 = row，竖图 = col
                this.$nextTick(() => {
                    let imgs = document.querySelectorAll('.project_cover');
                    imgs.forEach((item, index) => {
                        let img = new Image();
                        img.onload = () => {
                            this.list[index].loading = false;
                            if (img.width > img.height) {
                                item.classList.add('row');
                            } else {
                                item.classList.add('col');
                            }
                        }
                        img.src = item.src;
                    })
                    this.loading = false;
                })
            })
        },
        // 删除项目
        del(img_url) {
            let that = this;
            this.$confirm(that.lang.tips7, that.lang.tips6, {
                type: 'warning',
                closeOnPressEscape: false,
                closeOnClickModal: false,
                cancelButtonText: that.lang.tips3,
                confirmButtonText: that.lang.tips4,
                beforeClose(action, instance, done) {
                    if (action == 'confirm') {
                        if (that.httping) return;
                        that.httping = true;
                        let index = 0;
                        that.list.some((item, i) => {
                            if (item.img_url == img_url) {
                                index = i;
                                return true;
                            }
                        });
                        that.$ajax.get('/group/del_board_img/?img_url=' + img_url).then(r => {
                            that.list.splice(index, 1);
                            that.$message.success('success');
                            done();
                            setTimeout(() => {
                                that.httping = false;
                            }, 300);
                        });
                    } else {
                        done();
                    }
                }
            }).catch(() => { });
        },
        handleUpload(event) {
            // Handle the file upload logic here
        },
        save() {
            // Handle the save logic here
        },
        goBack() {
            this.$router.go(-1);
        },
        getDesignPreferenceLink() {
            return {
                name: 'upload',
                query: this.$route.query
            };
        },


    }
};
</script>
  
<style lang="less" scoped>
@import url('../tools/common.less');

.container {
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto;
    max-height: 500px;
}

.section {
    margin: 20px;
}

.section-title {
    font-weight: bold;
    display: flex;
    align-items: center;
}

.section-title .label-text {
    margin-right: 10px;
}

.section-content select {
    width: 280px;
    padding: 5px;
    margin-top: 5px;
}

/* Header Styles */
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 20px;
}

.center-text {
    text-align: center;
    flex-grow: 1;
}

.left-button {
    cursor: pointer;
    margin-right: 10px;
}

.title {
    font-size: 20px;
    font-weight: bold;
}

.upload {
    text-align: right;
}

.upload input[type="file"] {
    display: none;
}

.upload button {
    margin-left: 10px;
}

.content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 20px;
}

.form-row {
    display: flex;
    align-items: center;
    margin: 10px;
}

.form-row label {
    margin-right: 10px;
}

.image-gallery {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    justify-content: center;
    padding: 20px;
}

.image-item {
    flex-basis: calc(20% - 10px);
    margin-right: 10px;
    margin-bottom: 10px;
}

.image-item img {
    width: 100%;
    height: auto;
}

/* Footer Styles */
.footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    padding: 20px;
}

.footer button {
    margin-left: 10px;
}

.list {
    width: calc(100% - 60px);
    height: 100%;
    padding: 0 30px;
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    overflow-x: hidden;
    overflow-y: auto;

    .uploading {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.6);
        display: flex;
        justify-content: center;
        align-items: center;

        .box {
            width: 300px;
            padding: 50px 0;
            background: #ffffff;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            border-radius: 5px;

            p {
                width: 100%;
                text-align: center;
                margin-top: 10px;
                color: #888888;
            }
        }
    }

    .list_head {
        width: calc(100% - 20px);
        height: 40px;
        padding: 20px 10px;
        display: flex;
        align-items: center;

        .left-button {
            margin-right: auto;
            /* Push the button to the left side */
        }

        .design-style-preference {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-grow: 1;

            .text {
                font-weight: bold;
                font-size: 16px;
            }
        }

        .upload {
            margin-left: 20px;
            position: relative;

            input {
                width: 100px;
                height: 35px;
                opacity: 0;
                cursor: pointer;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 99;
            }
        }
    }

    .list_item {
        width: 100%;
        height: calc(100% - 80px);
        display: flex;
        justify-content: flex-start;
        align-content: flex-start;
        flex-wrap: wrap;
        overflow-x: hidden;
        overflow-y: auto;

        .nodata {
            width: 100%;
            height: 70%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .box {
            width: 200px;
            //height: 265px;
            background: #FFFFFF;
            border-radius: 12px;
            margin: 10px;
            padding: 15px;
            box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.0500);

            .main {
                width: 200px;
                height: 200px;
                background: #F5F5F7;
                border-radius: 8px;
                transition: all 0.3s;
                cursor: pointer;

                .cover {
                    width: 200px;
                    height: 200px;

                    img.row {
                        width: 100%;
                        border-radius: 8px;
                    }

                    img.col {
                        height: 100%;
                        border-radius: 8px;
                    }
                }
            }

            .title {
                text-align: left;
                font-weight: 600;
                font-size: 14px;
                color: #000000;
                margin-top: 15px;
                position: relative;

                p {
                    line-height: 25px;
                }

                p:last-child {
                    font-weight: 400;
                    font-size: 12px;
                    color: #86868B;
                }

                .action {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-50%) translateY(-50%);
                    -webkit-transform: translateX(-50%) translateY(-50%);
                    transition: all ease 0.35s;
                    width: 100%;
                    z-index: 2;
                    text-align: center;
                    opacity: 0;

                    i {
                        font-size: 14px;
                        color: #000000;
                        cursor: pointer;
                        border-radius: 50%;
                        padding: 8px;
                        margin: 0px 10px;
                        background-color: #FFFFFF;
                        box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
                    }
                }

                &:before {
                    background: #FFFFFF;
                    content: '';
                    height: 100%;
                    left: 0;
                    opacity: 0;
                    position: absolute;
                    top: 0;
                    transition: all ease 0.35s;
                    width: 100%;
                }
            }

            &:hover {
                .title .action {
                    opacity: 1;
                }

                .title:before {
                    opacity: 0.9;
                }
            }
        }
    }

    .list_item::-webkit-scrollbar {
        display: none;
    }
}
</style>
